import {
  List,
  ListProps,
  useListContext,
  useTranslate,
  TextField,
  DateField,
  useListSortContext,
  FunctionField
} from 'react-admin'
import React from 'react'
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core'
import { ArrowUpward, ArrowDownward } from '@material-ui/icons'
import { AccessDateDto } from '../../../core/dto/access-date.dto'

const AccessDatesRow = ({ record }: { record: AccessDateDto }) => {
  const translate = useTranslate()
  return (
    <>
      <TableRow>
        <TableCell>
          <DateField record={record} source="activeFrom" />
        </TableCell>
        <TableCell>
          <DateField record={record} source="activeTo" />
        </TableCell>
        <TableCell>
          <div>{record.timeFrom.substring(11, 19)}</div>
        </TableCell>
        <TableCell>
          <div>{record.timeTo.substring(11, 19)}</div>
        </TableCell>
        <TableCell>
          <FunctionField<AccessDateDto>
            record={record} render={(r?: AccessDateDto) => (
              translate(`resources.enums.accessDateTypes.${r?.type}`)
            )} />
        </TableCell>
        <TableCell>
          <TextField record={record} source="priority" />
        </TableCell>
        <TableCell style={{ textAlign: 'end' }}>
          <TextField
            record={record}
            source="dayOfWeek"
            label=" "
          />
        </TableCell>
      </TableRow>
    </>
  )
}

const AccessDatesGrid = () => {
  const translate = useTranslate()
  const { data, ids } = useListContext()
  const { currentSort, setSort } = useListSortContext()

  const inverseOrder = () => (currentSort.order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (field: string) => {
    setSort(field, currentSort.field === field ? inverseOrder() : 'DESC')
  }

  const HeaderCell = (name: string, display: string) => (
    <div style={{ display: 'flex', cursor: 'pointer', textAlign: 'center' }}>
      {currentSort.field === name && (
        <>{currentSort.order === 'ASC' ? <ArrowUpward /> : <ArrowDownward />}</>
      )}
      <div> {translate(display)} </div>
    </div>
  )

  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell onClick={() => handleSortChange('activeFrom')}>
              {HeaderCell('activeFrom', 'resources.access-dates.fields.activeFrom')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('activeTo')}>
              {HeaderCell('activeTo', 'resources.access-dates.fields.activeTo')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('timeFrom')}>
              {HeaderCell('timeFrom', 'resources.access-dates.fields.timeFrom')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('timeTo')}>
              {HeaderCell('timeTo', 'resources.access-dates.fields.timeTo')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('type')}>
              {HeaderCell('type', 'resources.access-dates.fields.type')}
            </TableCell>
            <TableCell>
              {translate('resources.access-dates.fields.priority')}
            </TableCell>
            <TableCell>
              {translate('resources.access-dates.fields.dayOfWeek')}
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <AccessDatesRow key={id} record={data[id] as AccessDateDto} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const AccessDatesList = (props: ListProps) => (
  <List
    {...props}
    exporter={false}
    perPage={20}
    sort={{ field: 'id', order: 'DESC' }}
  >
    <AccessDatesGrid />
  </List>
)
